<template>
    <div :key="entry.date" v-for="entry in entries">
        <CalendarEntry @deleteCE="$emit('deleteCE', entry.username, entry.time, entry.date)"
        :entry="entry"/>
    </div>
</template>

<script>
import CalendarEntry from './CalendarEntry'

export default {
    name:'CalendarEntries',
    props:{
        entries:Array,
        
    },
    components:{
        CalendarEntry,
    },
    emits:['deleteCE'],
}
</script>