<template>
    <div class='entry-buttons'>
   
        <button @click='onClick()'>All</button>
        <button @click='onClick2()'>Exercises</button>
        <button @click='onClick3()'>Meals</button>
        <button @click='onClick4()'>Breakfast</button>
        <button @click='onClick5()'>Lunch</button>
        <button @click='onClick6()'>Dinner</button>
        <button @click='onClick7()'>Snack</button>
    </div>
</template>

<script>
export default {
    name:'Button',
    props:{
        text:String,
        text2:String,
    },
    methods:{
        onClick(){
            this.$emit('toggle-view-entries')
        },
        onClick2(){
            this.$emit('toggle-view-exercises')
            //for viewing exercises
        },
        onClick3(){
            this.$emit('toggle-view-meals')
            //for viewing meals
        },
        onClick4(){
            this.$emit('toggle-view-breakfast')
            //for viewing 
        },
        onClick5(){
            this.$emit('toggle-view-lunch')
            //for viewing 
        },
        onClick6(){
            this.$emit('toggle-view-dinner')
            //for viewing 
        },
        onClick7(){
            this.$emit('toggle-view-snack')
            //for viewing 
        },
        onClick8(){
            this.$emit('create-entry')
            //to create a new entry
        },
    },
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
button{
	font-size:30px;
	color:#1abc9c;
    border: 1px groove;
    padding: 10px;
    margin:5px;
    font-family:"Poppins";
}
button:hover{
    cursor:pointer;
    font-weight:bold;
}
.create-entry button{
    color:white;
    background-color: #1abc9c;
}
</style>