<template>
<div class = 'register'>
     
            <button v-on:click="Back"  class="button">Back to Calendar</button>
    
    <h1>Update Entry</h1>
    <form>
        <label for="name">The Name of the Entry:</label><br>
        <input type="text" id = "name" v-model="updatedEntry.name" placeholder="Name" required ="yes"><br>
        <label>Entry Type:<br>
        <select id ="type" v-model="updatedEntry.type" required = "yes">
            <option value="">Select a Type</option>
            <option value="exercise">Exercise</option>
            <option value="breakfast">Breakfast</option>
            <option value="lunch">Lunch</option>
            <option value="dinner">Dinner</option>
            <option value="snack">Snack</option>
        </select></label><br>
        <label for="description">Description:</label><br>
        <textarea id = "description" v-model="updatedEntry.description" required="yes"/><br>
        <label for="calories">Calories:<br>
        <input id = "calories" type=int v-model="updatedEntry.calories" placeholder="calories" required="yes">
        </label><br><br>
        
        <button v-on:click="updateEntry" class="button" type="button">Update Entry</button>
        
    </form>
</div>
</template>
<script>
import FormSubmission from '../FormSubmission'
// import FormSubmission from '../FormSubmission';
// import CalendarInfo from '../CalendarInfo'

export default {
    name: 'UpdateEntry',
    data(){
        return{
            updatedEntry: {
                name:"",
                type:"",
                description:"",
                calories:0,
                username:""
                },
            currentEntry:{
                name:"",
                type:"",
                description:"",
                calories:0,
                username:""
            },
            entryExists:[],
            universalUsername:''
            
            }
        },
        created(){
            this.currentEntry.name = this.$route.params.name
            this.updatedEntry.name = this.currentEntry.name
            this.currentEntry.type = this.$route.params.type
            this.updatedEntry.type = this.currentEntry.type
            this.currentEntry.description = this.$route.params.description
            this.updatedEntry.description = this.currentEntry.description
            this.currentEntry.calories = this.$route.params.calories
            this.updatedEntry.calories = this.currentEntry.calories
            this.currentEntry.username = this.$route.params.username
            this.updatedEntry.username = this.currentEntry.username
            this.universalUsername = this.$route.query.universalUsername
            
        },
        methods: {
            async updateEntry() {
                if(this.updatedEntry.name =='' || this.updatedEntry.type =='' || this.updatedEntry.description ==''){
                    alert("Please fill out all of the specified fields.")
                    
                }else{
                    this.entryExists = await FormSubmission.getEntry(this.updatedEntry.name,this.updatedEntry.type,this.updatedEntry.username)
                    console.log(this.entryExists)
                    if(this.entryExists[0] == undefined || this.updatedEntry.name == this.currentEntry.name && this.updatedEntry.type == this.currentEntry.type){
                        await FormSubmission.updateEntry(this.currentEntry.name,this.currentEntry.type,this.currentEntry.username,this.updatedEntry.name,this.updatedEntry.type,this.updatedEntry.description,this.updatedEntry.calories)
                        alert("This Entry has been updated.")
                        this.$router.push({name:"Calendar",query:{universalUsername:this.universalUsername}})
                    }
                    else{
                        alert("An Entry has already been created with the specified name and type")
                    }
                  
                }
                console.log(this.currentEntry)
                console.log(this.updatedEntry)
                
            },
            Back(){
            this.$router.push({name:'Calendar',query:{universalUsername:this.universalUsername}})
        },
            
        }
        }
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
.register{
    font-family:'Poppins';
    padding:10px;
    text-align:center;
}
.button{
    color: white;
    border:1px solid black;
    background-color: #1abc9c;
}
label{
    font-weight:bold;
}
.submit{
    border:groove;
    border-radius: 40px;
    border-color:cornsilk;
    background-color:none;
    color:#1abc9c;
    font-size: 20px;
    font-family: inherit;
    font-weight: bold;
    padding:5px;
}
</style>