<template>
<div class='body'>
<div :style="{'margin-left': `40px`}">
<div style="background-color:black; width:75%; margin:30px auto; padding:5px; border-radius:5px;">
<ul class='current-week'>
	<li style='font-size:30px;font-weight:bold;'>Week</li>
	<li>
	<i style='float:left;' v-on:click="lastWeek">&#10094;</i>
	Sunday, {{this.sunday.toLocaleDateString()}} - Saturday, {{this.saturday.toLocaleDateString()}}
	<i style='float:right;' v-on:click="nextWeek">&#10095;</i>
	</li>
</ul>
</div>
<div class='calendar-entries-wrapper'>
<table class = 'days'>
	<tr>
		<th class='day'>Sunday<br>{{this.sunday.toLocaleDateString()}}</th>
		<td
		v-on:drop='sunDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="sunEntries" />
		</div>
		</td>
		<!--bc we only want the placeholders to be droppable-->
	</tr>
	<tr>
		<th class='day'>Monday<br>{{this.monday.toLocaleDateString()}}</th>
		<td
		v-on:drop='monDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="monEntries" />
		</div>
		</td>
	</tr>
	<tr>
		<th class='day'>Tuesday<br>{{this.tuesday.toLocaleDateString()}}</th>
		<td
		v-on:drop='tuesDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="tuesEntries" />
		</div>
		</td>
	</tr>
	<tr>
		<th class='day'>Wednesday<br>{{this.wednesday.toLocaleDateString()}}</th>
		<td
		v-on:drop='wedDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="wedEntries" />
		</div>
		</td>
	</tr>
	<tr>
		<th class='day'>Thursday<br>{{this.thursday.toLocaleDateString()}}</th>
		<td
		v-on:drop='thursDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="thursEntries" />
		</div>
		</td>
	</tr>
	<tr>
		<th class='day'>Friday<br>{{this.friday.toLocaleDateString()}}</th>
		<td
		v-on:drop='friDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="friEntries" />
		</div>
		</td>
	</tr>
	<tr>
		<th class='day'>Saturday<br>{{this.saturday.toLocaleDateString()}}</th>
		<td
		v-on:drop='satDrop'
		v-on:dragover="allowDrop">
		<div class='entries-content2'>
			<CalendarEntries  @deleteCE='deleteCE' :entries="satEntries" />
		</div>
		</td>
	</tr>
</table>
<div class='entries'>
	<h1>Entries</h1>
	
		<button v-on:click="CreateEntry" style='color:white; background-color:#1abc9c; font-size:30px;border:1px groove; padding:10px; margin:5px;'>Create Entry</button>
	<br>
	<Button
	@toggle-view-entries="toggleViewEntries"
	@toggle-view-exercises="toggleViewExercises"
	@toggle-view-meals="toggleViewMeals"
	@toggle-view-breakfast="toggleViewBreakfast"
	@toggle-view-lunch="toggleViewLunch"
	@toggle-view-dinner="toggleViewDinner"
	@toggle-view-snack="toggleViewSnack"/>
	<div>
		<div class='entries-content' v-if="viewAllEntries">
			<Entries  @delete-entry='deleteEntry' :entries="entries"/>	
		</div>
		<div class='entries-content' v-else-if="viewExerciseEntries">
			<Entries  @delete-entry='deleteEntry' :entries="exerciseEntries"/>
		</div>
		<div class='entries-content' v-else-if="viewMealEntries">
			<Entries  @delete-entry='deleteEntry' :entries="mealEntries"/>
		</div>
		<div class='entries-content' v-else-if="viewBreakfastEntries">
			<Entries  @delete-entry='deleteEntry' :entries="breakfastEntries"/>
		</div>
		<div class='entries-content' v-else-if="viewLunchEntries">
			<Entries  @delete-entry='deleteEntry' :entries="lunchEntries"/>
		</div>
		<div class='entries-content' v-else-if="viewDinnerEntries">
			<Entries  @delete-entry='deleteEntry' :entries="dinnerEntries"/>
		</div>
		<div class='entries-content' v-else-if="viewSnackEntries">
			<Entries  @delete-entry='deleteEntry' :entries="snackEntries"/>
		</div>
	</div>
</div>
</div>
<form class = 'dropdown' v-show='dropDownBoolean'>
	<i @click ="dropDownX" class ="fas fa-times"></i>
    
	<div class = 'dropdown-content'>		
    <label for="time">Please select a time:</label>
    <select id='time' v-model="holdDate.hour">
		<option value="">Hours</option>
		<option value ="1">1</option>
		<option value ="2">2</option>
		<option value ="3">3</option>
		<option value ="4">4</option>
		<option value ="5">5</option>
		<option value ="6">6</option>
		<option value ="7">7</option>
		<option value ="8">8</option>
		<option value ="9">9</option>
		<option value ="10">10</option>
		<option value ="11">11</option>
		<option value ="12">12</option>
	</select>

    <select id='time' v-model="holdDate.minutes">
		<option value="">Minutes</option>
		<option value ="00">00</option>
		
		<option value ="05">05</option>
		
		<option value ="10">10</option>
		
		<option value ="15">15</option>
		
		<option value ="20">20</option>
		
		<option value ="25">25</option>
		
		<option value ="30">30</option>

		<option value ="35">35</option>
		
		<option value ="40">40</option>

		<option value ="45">45</option>
		
		<option value ="50">50</option>

		<option value ="55">55</option>
	</select>
    
    
    <select id="AM/PM" v-model="holdDate.ampm" >
		<option value="">Select AM or PM</option>
		<option value ="AM">AM</option>
		<option value ="PM">PM</option>
	</select>
	<br>
	<button id='submit' type = 'button' v-on:click='closeDropDown' v-on:hover="test()" >Add Entry to Calendar</button>
</div>
</form>
</div>
	<div>
		<Sidebar :universalUsername="$route.query.universalUsername" />
	</div>
</div>
</template>

<script>
import Button from './Button'
import Entries from './Entries'
import CalendarInfo from '../CalendarInfo'
import CalendarEntries from './CalendarEntries'
import FormSubmission from '../FormSubmission'
import Sidebar from './sidebar/Sidebar.vue'

export default {
  name: 'Calendar',
  components:{
	Sidebar,
	Button,
	Entries,
	CalendarEntries
  },
  emits: ['toggle-view-entries','toggle-view-exercises','toggle-view-meals','toggle-view-breakfast','toggle-view-lunch','toggle-view-dinner','toggle-view-snack','deleteCE','delete-entry'],
  data(){
	return{
	entries:[],
	exerciseEntries:[],
	mealEntries:[],
	breakfastEntries:[],
	lunchEntries:[],
	dinnerEntries:[],
	snackEntries:[],
	error:'',
	viewAllEntries:true,
	viewExerciseEntries:false,
	viewMealEntries:false,
	viewBreakfastEntries:false,
	viewLunchEntries:false,
	viewDinnerEntries:false,
	viewSnackEntries:false,
	dropDownBoolean:false,

	currentDate: new Date(),
	sunday:new Date(),
	monday:new Date(),
	tuesday:new Date(),
	wednesday:new Date(),
	thursday:new Date(),
	friday:new Date(),
	saturday:new Date(),

	holdEntry: [],
	holdDate:{
		hour:"",
		minutes:"",
		ampm:"",
		dayOfWeek:"",
		date:""

	},
	sunEntries: [],
	monEntries: [],
	tuesEntries: [],
	wedEntries: [],
	thursEntries: [],
	friEntries: [],
	satEntries: [],
	//entries (except All) are hidden by default

	universalUsername:''
	}
  },
  async created(){
	this.universalUsername = this.$route.query.universalUsername
	try{
    this.entries = await CalendarInfo.getEntries(this.universalUsername)
	this.exerciseEntries = await CalendarInfo.getExerciseEntries(this.universalUsername)
	this.mealEntries = await CalendarInfo.getMealEntries(this.universalUsername)
	this.breakfastEntries = await CalendarInfo.getBreakfastEntries(this.universalUsername)
	this.lunchEntries = await CalendarInfo.getLunchEntries(this.universalUsername)
	this.dinnerEntries = await CalendarInfo.getDinnerEntries(this.universalUsername)
	this.snackEntries = await CalendarInfo.getSnackEntries(this.universalUsername)

	this.currentDate.setHours(0)
	this.currentDate.setMinutes(0)
	this.currentDate.setSeconds(0)
	this.currentDate.setMilliseconds(0)
		
		//Sunday is equal to 0
		if(this.currentDate.getDay() == 0){
			this.sunday = this.currentDate
			console.log(this.sunday)
			this.monday.setDate(this.sunday.getDate() + 1)
			console.log(this.monday)
			this.tuesday.setDate(this.sunday.getDate() + 2)
			console.log(this.tuesday)
			this.wednesday.setDate(this.sunday.getDate() + 3)
			console.log(this.sunday)
			this.thursday.setDate(this.sunday.getDate() + 4)
			console.log(this.thursday)
			this.friday.setDate(this.sunday.getDate() + 5)
			console.log(this.friday)
			this.saturday.setDate(this.sunday.getDate()+ 6)
			console.log(this.saturday)
		}
		//Monday is equal to 1
		else if(this.currentDate.getDay() == 1){
			this.monday = this.currentDate
			console.log(this.monday)
			this.sunday.setDate(this.monday.getDate() - 1)
			console.log(this.sunday)
			this.tuesday.setDate(this.monday.getDate() + 1)
			console.log(this.tuesday)
			this.wednesday.setDate(this.monday.getDate() + 2)
			console.log(this.sunday)
			this.thursday.setDate(this.monday.getDate() + 3)
			console.log(this.thursday)
			this.friday.setDate(this.monday.getDate() + 4)
			console.log(this.friday)
			this.saturday.setDate(this.monday.getDate()+ 5)
			console.log(this.saturday)
		}
		//Tuesday is equal to 2
		else if(this.currentDate.getDay() == 2){
			this.tuesday = this.currentDate
			console.log(this.tuesday)
			this.monday.setDate(this.tuesday.getDate() - 1)
			console.log(this.monday)
			this.sunday.setDate(this.tuesday.getDate() - 2)
			console.log(this.sunday)
			this.wednesday.setDate(this.tuesday.getDate() + 1)
			console.log(this.sunday)
			this.thursday.setDate(this.tuesday.getDate() + 2)
			console.log(this.thursday)
			this.friday.setDate(this.tuesday.getDate() + 3)
			console.log(this.friday)
			this.saturday.setDate(this.tuesday.getDate()+ 4)
			console.log(this.saturday)
		}
		//Wednesday is equal to 3
		else if(this.currentDate.getDay() == 3){
			this.wednesday = this.currentDate
			console.log(this.wednesday)
			this.sunday.setDate(this.wednesday.getDate() - 3)
			console.log(this.sunday)
			this.monday.setDate(this.wednesday.getDate() - 2)
			console.log(this.monday)
			this.tuesday.setDate(this.wednesday.getDate() - 1)
			console.log(this.tuesday)
			this.thursday.setDate(this.wednesday.getDate() + 1)
			console.log(this.thursday)
			this.friday.setDate(this.wednesday.getDate() + 2)
			console.log(this.friday)
			this.saturday.setDate(this.wednesday.getDate()+ 3)
			console.log(this.saturday)
		}
		//Thursday is equal to 4
		else if(this.currentDate.getDay() == 4){
			this.thursday = this.currentDate
			this.monday.setDate(this.thursday.getDate() - 3)
			this.tuesday.setDate(this.thursday.getDate() - 2)
			this.wednesday.setDate(this.thursday.getDate() - 1)
			this.sunday.setDate(this.thursday.getDate() - 4)
			this.friday.setDate(this.thursday.getDate() + 1)
			this.saturday.setDate(this.thursday.getDate()+ 2)
		}
		//Friday is equal to 5
		else if(this.currentDate.getDay() == 5){
			this.friday = this.currentDate
			this.monday.setDate(this.friday.getDate() - 4)
			this.tuesday.setDate(this.friday.getDate() - 3)
			this.wednesday.setDate(this.friday.getDate() -2)
			this.thursday.setDate(this.friday.getDate() - 1)
			this.sunday.setDate(this.friday.getDate() - 5)
			this.saturday.setDate(this.friday.getDate() + 1)
		
		}
		//Saturday is equal to 6
		else if(this.currentDate.getDay() == 6){
			this.saturday = this.currentDate
			
			this.monday.setDate(this.saturday.getDate() - 5)
			
			this.tuesday.setDate(this.saturday.getDate() - 4)
			
			this.wednesday.setDate(this.saturday.getDate() - 3)
		
			this.thursday.setDate(this.saturday.getDate() - 2)
		
			this.friday.setDate(this.saturday.getDate() - 1)
	
			this.sunday.setDate(this.saturday.getDate() - 6)
		
		}
		else{
			alert("shouldn't be here rn")
		}
		this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
		this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
		this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
		this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
		this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
		this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
		this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)
    }catch(err){
    this.error = err.message;
    }
	
	
  },
  methods:{
	toggleViewEntries(){
		console.log('View All Entries')
		this.viewAllEntries = true
		this.viewExerciseEntries = false
		this.viewMealEntries=false
		this.viewBreakfastEntries=false
		this.viewLunchEntries=false
		this.viewDinnerEntries=false
		this.viewSnackEntries=false
	},
	toggleViewExercises(){
		console.log('View Exercises')
		this.viewAllEntries = false
		this.viewExerciseEntries = true
		this.viewMealEntries=false
		this.viewBreakfastEntries=false
		this.viewLunchEntries=false
		this.viewDinnerEntries=false
		this.viewSnackEntries=false
	},
	toggleViewMeals(){
		console.log('View Meals')
		this.viewExerciseEntries = false
		this.viewAllEntries = false
		this.viewMealEntries=true
		this.viewBreakfastEntries=false
		this.viewLunchEntries=false
		this.viewDinnerEntries=false
		this.viewSnackEntries=false
	},
	toggleViewBreakfast(){
		console.log('View Breakfast Entries')
		this.viewExerciseEntries = false
		this.viewAllEntries = false
		this.viewMealEntries=false
		this.viewBreakfastEntries=true
		this.viewLunchEntries=false
		this.viewDinnerEntries=false
		this.viewSnackEntries=false
	},
	toggleViewLunch(){
		console.log('View Lunch Entries')
		this.viewExerciseEntries = false
		this.viewAllEntries = false
		this.viewMealEntries=false
		this.viewBreakfastEntries=false
		this.viewLunchEntries=true
		this.viewDinnerEntries=false
		this.viewSnackEntries=false
	},
	toggleViewDinner(){
		console.log('View Dinner Entries')
		this.viewExerciseEntries = false
		this.viewAllEntries = false
		this.viewMealEntries=false
		this.viewBreakfastEntries=false
		this.viewLunchEntries=false
		this.viewDinnerEntries=true
		this.viewSnackEntries=false
	},
	toggleViewSnack(){
		console.log('View Snack Entries')
		this.viewExerciseEntries = false
		this.viewAllEntries = false
		this.viewMealEntries=false
		this.viewBreakfastEntries=false
		this.viewLunchEntries=false
		this.viewDinnerEntries=false
		this.viewSnackEntries=true
	},
	async deleteEntry(name,username){
		if(confirm('Are you sure you wish to delete "' + name + '" ' + username +'?' )){
		await CalendarInfo.deleteEntry(name,username)
		this.entries = await CalendarInfo.getEntries(this.universalUsername)
		this.exerciseEntries = await CalendarInfo.getExerciseEntries(this.universalUsername)
		this.mealEntries = await CalendarInfo.getMealEntries(this.universalUsername)
		this.breakfastEntries = await CalendarInfo.getBreakfastEntries(this.universalUsername)
		this.lunchEntries = await CalendarInfo.getLunchEntries(this.universalUsername)
		this.dinnerEntries = await CalendarInfo.getDinnerEntries(this.universalUsername)
		this.snackEntries = await CalendarInfo.getSnackEntries(this.universalUsername)
		}
	},
	async deleteCE(username,time,date){
		if(confirm('Are you sure you wish to delete this entry ' + username +'?' )){
			await CalendarInfo.deleteCE(username,time,date)
			this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
			this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
			this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
			this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
			this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
			this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
			this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)
		}
	},
	allowDrop:function(event){
		event.preventDefault();
		//so that element can actually be dropped
	},
		async sunDrop(event){
			event.preventDefault();

			//retrieves data set in setData method
			var sunData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			//so entries display next to each other
			//event.target.style.display='flex';
			
			el.innerHTML = sunData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "sunday"
			this.holdDate.date = this.sunday.toDateString()

			this.dropDownBoolean=true;

			console.log(el.querySelector('h3').getAttribute('id') + " was dropped on Sunday.")
		},
		async monDrop(event){
			event.preventDefault();

			var monData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = monData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "monday"
			this.holdDate.date = this.monday.toDateString()

			this.dropDownBoolean=true;

			
		},
		async tuesDrop(event){
			event.preventDefault();

			var tuesData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = tuesData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "tuesday"
			this.holdDate.date =  this.tuesday.toDateString()

			this.dropDownBoolean=true;

			
		},
		async wedDrop(event){
			event.preventDefault();

			var wedData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = wedData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "wednesday"
			this.holdDate.date = this.wednesday.toDateString()

			this.dropDownBoolean=true;
		},
		async thursDrop(event){
			event.preventDefault();

			var thursData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = thursData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "thursday"
			this.holdDate.date = this.thursday.toDateString()

			this.dropDownBoolean=true;


		},
		async friDrop(event){
			event.preventDefault();

			var friData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = friData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "friday"
			this.holdDate.date = this.friday.toDateString()

			this.dropDownBoolean=true;

			
		},
		async satDrop(event){
			event.preventDefault();

			var satData = event.dataTransfer.getData('text');
			var el = document.createElement('html');

			event.target.style.display='flex';
			el.innerHTML = satData;

			var temp = el.querySelector('h3').getAttribute('id')
			var temp2 = temp.split(":")
			console.log(temp + " was dropped on Saturday.")

			this.holdEntry = await FormSubmission.getEntry(temp2[0],temp2[1],this.universalUsername)
			this.holdDate.dayOfWeek = "saturday"
			this.holdDate.date = this.saturday.toDateString()
			

			this.dropDownBoolean = true;
		},
	dropDownX(){

			if(confirm('Close without saving?')){
			this.dropDownBoolean=false;
			}
		},
	async closeDropDown(){
		if(this.holdDate.hour != "" || this.holdDate.minutes != "" || this.holdDate.ampm != ""){
			const temp = await CalendarInfo.getSpecificEntry(this.holdDate.date,this.holdDate.hour+":"+this.holdDate.minutes+" "+this.holdDate.ampm)
			if(temp[0] == undefined){
			await CalendarInfo.createCalendarEntry({
				day:this.holdDate.dayOfWeek,
                date: this.holdDate.date,
                time: this.holdDate.hour+":"+this.holdDate.minutes+" "+this.holdDate.ampm,
                entryID: this.holdEntry[0]._id,
				username: this.holdEntry[0].username
			})
			this.dropDownBoolean=false
			alert("Entry added to calendar")
			this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
			this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
			this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
			this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
			this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
			this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
			this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)
			console.log(this.holdEntry[0]._id)
			}else{
				alert("An entry has already been added for " + this.holdDate.dayOfWeek + " at the specified time")
			}
		}else{
			alert("Please fill out all specified fields")
		}
		
	},

	
	async nextWeek(){
		this.sunday = new Date(this.sunday.getFullYear(),this.sunday.getMonth(),this.sunday.getDate()+7)
		this.saturday = new Date(this.saturday.getFullYear(),this.saturday.getMonth(),this.saturday.getDate()+7)
		this.monday = new Date(this.monday.getFullYear(),this.monday.getMonth(),this.monday.getDate()+7)
		this.tuesday = new Date(this.tuesday.getFullYear(),this.tuesday.getMonth(),this.tuesday.getDate()+7)
		this.wednesday = new Date(this.wednesday.getFullYear(),this.wednesday.getMonth(),this.wednesday.getDate()+7)
		this.thursday = new Date(this.thursday.getFullYear(),this.thursday.getMonth(),this.thursday.getDate()+7)
		this.friday = new Date(this.friday.getFullYear(),this.friday.getMonth(),this.friday.getDate()+7)
		
		this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
		this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
		this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
		this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
		this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
		this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
		this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)
	},
	async lastWeek(){
		this.sunday = new Date(this.sunday.getFullYear(),this.sunday.getMonth(),this.sunday.getDate()-7)
		this.saturday = new Date(this.saturday.getFullYear(),this.saturday.getMonth(),this.saturday.getDate()-7)
		this.monday = new Date(this.monday.getFullYear(),this.monday.getMonth(),this.monday.getDate()-7)
		this.tuesday = new Date(this.tuesday.getFullYear(),this.tuesday.getMonth(),this.tuesday.getDate()-7)
		this.wednesday = new Date(this.wednesday.getFullYear(),this.wednesday.getMonth(),this.wednesday.getDate()-7)
		this.thursday = new Date(this.thursday.getFullYear(),this.thursday.getMonth(),this.thursday.getDate()-7)
		this.friday = new Date(this.friday.getFullYear(),this.friday.getMonth(),this.friday.getDate()-7)
		
		this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
		this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
		this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
		this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
		this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
		this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
		this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)
	},

	CreateEntry(){
		this.$router.push({name:'CreateEntry',query:{universalUsername:this.universalUsername}})
	}
  }
  }


</script>

