<template>
<div id = 'calendar-entry' @mousemove="hover">
    <h3>{{ testEntry.name}}
    <p>Calories: {{testEntry.calories}}</p>
    <p>Type: {{testEntry.type}}</p>
    <p>Time: {{entry.time}}</p>
    <span>Description: {{testEntry.description}}
    <br>
    <i v-on:click ="onDeleteCE(entry.username,entry.time,entry.date)" class ="fas fa-times"></i>
   
    <button v-on:click='onEditCE()'>Edit</button>
    
    
    
    
    </span>
    </h3>
</div>
</template>
<script>
import CalendarInfo from '../CalendarInfo'

export default {
    name:'CalendarEntry',

    props:{
        entry: Object,
        entryName: String
    },
     data(){
        return{
            testEntry:{
                day:'',
                date:'',
                time:'',
                name:'',
                type:'',
                description:'',
                calories:0,
                username:''
            },
            loadEntry:[]
           
            
        }
    },
    async created(){
        try {
            this.loadEntry = await CalendarInfo.getEntryforCE(this.entry.entryID)
        } catch (error) {
            this.error = error.message;
        }
        this.testEntry.name = this.loadEntry[0].name
        this.testEntry.type = this.loadEntry[0].type
        this.testEntry.description = this.loadEntry[0].description
        this.testEntry.calories = this.loadEntry[0].calories
        
    },
    methods:{
    onDeleteCE(){
        this.$emit("deleteCE")
    },
    onEditCE(){
    this.$router.push({name:'UpdateCalendarEntry', params:{
                day:this.testEntry.day,
                date:this.testEntry.date,
                time:this.testEntry.time,
                username:this.testEntry.username
        },
        query:{universalUsername:this.testEntry.username}})
        console.log(this.testEntry)
    },
    async hover(){
        
        this.testEntry.day = this.entry.day
        this.testEntry.date = this.entry.date
        this.testEntry.time = this.entry.time
        this.testEntry.username = this.entry.username
        this.testEntry.name = this.loadEntry[0].name
        this.testEntry.type = this.loadEntry[0].type
        this.testEntry.calories = this.loadEntry[0].calories
        this.testEntry.description = this.loadEntry[0].description
        
        
    },
    dragStart:function(event){
    //grabs the HTML in text format (can be named whatever)
	event.dataTransfer.setData('text', event.target.innerHTML)
	},

	dragging:function(){
        console.log(" '"+ this.entry.name + "' is being dragged.")
	},
    },
    
    
}
</script>
<style scoped>
#calendar-entry{
    background: #095546;
    border-radius: 10px;
    padding: 5px;
    text-align: left;
    font-size:10.5px;
    width:150px;
    display: grid;
    grid-gap:8px;
}
.btn{
    display:none;
}
button{
    color:#1abc9c;
    border:1px groove;
}
button:hover{
    cursor:pointer;
    font-weight: bold;
}
.fas{
    color:red;
}
.fas:hover{
    cursor:pointer;
}
p{
    font-weight: normal;
    line-height:1em;
}
h3:hover p{
    display:none;
}
h3 span{
    display:none;
}
h3:hover span{
    display:block;
    font-weight: normal;
}
</style>