<template>
    <div class = 'register'>
    <form action = 'profile.php' method = "POST">
        <h1>Create Account</h1>
        <label for="username">Username:<br><input type="text" id =  "username" name="username" placeholder="Username" v-model="this.newUser.username" required ="yes"></label><br>
        <label for="email">E-mail:<br><input id = "email" type="email" name="email" placeholder="E-mail" v-model="this.newUser.email" required="yes"></label><br>
        <label for="password">Password:<br><input id = "password" type="password" name="password"  v-model="this.newUser.password" required="yes">
        <img :src="require('@/assets/showpass.png')"
        @click='showPass()' style = "width:1.25%; position:absolute;left: auto;"></label><br><br>
        <label>current weight (lbs)<br><input type="number" id = "cweight" name="cweight" v-model="this.newUser.currentWeight" required =yes></label><br><br>
        <label>goal weight (lbs)<br><input type="number" id = "gweight" name="gweight" v-model="this.newUser.goalWeight" required =yes></label><br><br>
        <button v-on:click="createUser" class="button" type="button">Create Account</button>
    </form>
    </div>
</template>
<script>
import FormSubmission from '../FormSubmission'
    export default{
        name: 'CreateAccount',
         data(){
        return{
            newUser: {
                username:'',
                email:'',
                password:'',
                currentWeight:0,
                goalWeight:0
                },
            usernameTaken:[],
            emailTaken:[],
            
            
            }
        },
        created(){

        },
        methods:{
            showPass(){
                var p = document.getElementById('password')
                if(p.type == 'password'){
                    p.type ='text';
                }else{
                    p.type = 'password';
                }
            },
            async createUser() {
                if(this.newUser.username =='' || this.newUser.email =='' || this.newUser.password ==''){
                    alert("Please fill out all of the specified fields.")
                    
                }else{
                    this.usernameTaken = await FormSubmission.checkUsername(this.newUser.username)
                    this.emailTaken = await FormSubmission.checkEmail(this.newUser.email)
                    console.log(this.usernameTaken[0])
                    console.log(this.emailTaken[0])
                    if(this.usernameTaken[0] == undefined){
                        
                        if(this.emailTaken[0] == undefined){
                        
                        await FormSubmission.createUser(this.newUser)
                        alert("Your Account has been successfully created.")
                        console.log(this.newUser)
                        this.$router.push({name:"Homepage", query:{universalUsername:this.newUser.username}})
                        
                        }else{
                            alert("An Account has already been created with the specified email.")
                        
                        }
                        
                    }else{
                        alert("An Account has already been created with the specified username.")
                       
                    }
                }
            },
        }
    }
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

.register{
    padding:10px;
    text-align:center;
    font-family: "Poppins";
}
.button{
    color: white;
    border:1px solid black;
    background-color: #1abc9c;
}
label{
    font-weight:bold;
}
.submit{
    border:groove;
    border-radius: 40px;
    border-color:cornsilk;
    background-color:none;
    color:#1abc9c;
    font-size: 20px;
    font-family: inherit;
    font-weight: bold;
    padding:5px;
}
</style>