import axios from "axios";

const url = 'api/homepage/';

 class LoadEntries {

    //Entry UI

    static getEntries() {
        return new Promise ((resolve,reject) => {
            axios.get(url).then((res) => {
                const data = res.data;
                resolve(
                    data.map(entry => ({
                        _id: entry._id,
                        type: entry.type,
                        name: entry.name,
                        description: entry.description,
                        calories: entry.calories,
                        
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    
}


export default LoadEntries;