<template>
    <div class = 'register'>
    <form action = 'profile.php' method = "POST">
        <h1>FitHut</h1>
        <h2>Login:</h2>
        <label for="username">Username:<br><input type="text" id =  "username" name="username" placeholder="Username" v-model="this.user.username" required ="yes"></label><br>
        
        <label for="password">Password:<br><input id = "password" type="password" name="password"  v-model="this.user.password" required="yes">
        <img :src="require('@/assets/showpass.png')"
        @click='showPass()' style = "width:1.25%; position:absolute;left: auto;"></label><br><br>
        
        <button v-on:click="Login" class="button" type="button">Login</button>
    </form>

    <div>
        <h1>Don't have an Account?</h1>
        <button v-on:click="createAccount" class="button" type="button">Create one Here!</button>
    </div>
    </div>
</template>
<script>
import FormSubmission from '../FormSubmission'
    export default{
        name: 'Login',
         data(){
        return{
            user: {
                username:'',
                email:'',
                password:'',
                currentWeight:0,
                goalWeight:0
                },
            usernameLogin:[],
            emailLogin:[],
            
            
            }
        },
        created(){

        },
        methods:{
            showPass(){
                var p = document.getElementById('password')
                if(p.type == 'password'){
                    p.type ='text';
                }else{
                    p.type = 'password';
                }
            },
            async Login() {
                if(this.user.username =='' || this.user.password ==''){
                    alert("Please fill out all of the specified fields.")
                    
                }else{
                    this.usernameLogin = await FormSubmission.loginUsername(this.user.username,this.user.password)
                    

                    if(this.usernameLogin[0] != undefined){

                        alert("Login Successful")
                        this.$router.push({name:"Homepage", query:{universalUsername:this.usernameLogin[0].username}})
                        
                    }else{
                        alert("Incorrect username or password")
                       
                    }
                }
            },

            createAccount(){
                this.$router.push({name:"CreateAccount"})
            }
        }
    }
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

.register{
    padding:10px;
    text-align:center;
    font-family: "Poppins";
}
img{
    width:2.5%;
}
.button{
    color: white;
    border:1px solid black;
    background-color: #1abc9c;
}
label{
    font-weight:bold;
}
.submit{
    border:groove;
    border-radius: 40px;
    border-color:cornsilk;
    background-color:none;
    color:#1abc9c;
    font-size: 20px;
    font-family: inherit;
    font-weight: bold;
    padding:5px;
}
</style>