<template>
<div>
    <div :style="{'margin-left': `40px`}">
    <div class = 'container'>
        <div class = 'basic-information'>
        <h1>Hello, {{this.$route.query.universalUsername}}</h1>
            <img :src ="require('@/assets/profile-pic.jpg')">
        </div>
        <div class = 'this-week'>
        <h1 style='text-transform:uppercase;'>Current Week's Stats</h1>
        <hr>
        <div class='this-week-content'>
            <!--load This Week Component here-->
            <Stat :stat='{numofmeals:this.totalMeal,numofexercise:this.totalExercise,totalentriescompleted:this.totalEntries,caloriesgained:this.caloriesGained,calorieslost:this.caloriesLost}'/>
        </div>
        </div>
        
    </div>
    <div id="myPlot">

    </div>
    <br>
    <div id="calories">

    </div>
    </div>
    <div>
		<Sidebar :universalUsername="$route.query.universalUsername" />
	</div>
</div>
</template>

<script >
import Plotly from "plotly.js";

import Stat from './Stat'
import Sidebar from './sidebar/Sidebar.vue'
import CalendarInfo from '../CalendarInfo'

export default {
  name: 'User',
  data(){
      return{
            stats:[],
            universalUsername:'',
            currentDate: new Date(),
            sunday:new Date(),
            monday:new Date(),
            tuesday:new Date(),
            wednesday:new Date(),
            thursday:new Date(),
            friday:new Date(),
            saturday:new Date(),
            error:'',
            sunEntries: [],
            monEntries: [],
            tuesEntries: [],
            wedEntries: [],
            thursEntries: [],
            friEntries: [],
            satEntries: [],
            allEntries: [],
            caloriesGained:0,
            caloriesLost:0,
            totalExercise:0,
            totalMeal:0,
            totalEntries:0,
            oneWeekEntries:[],
            twoWeekEntries:[],
            threeWeekEntries:[],
            pastStats:[
                {
                    caloriesgained:0,
                    calorieslost:0,
                    numofexercise:0,
                    numofmeals:0,
                    totalentriescompleted:0
                },
                {
                    caloriesgained:0,
                    calorieslost:0,
                    numofexercise:0,
                    numofmeals:0,
                    totalentriescompleted:0
                },
                {
                    caloriesgained:0,
                    calorieslost:0,
                    numofexercise:0,
                    numofmeals:0,
                    totalentriescompleted:0
                }
            ]
      }
  },
  components:{
      
      Stat,
      Sidebar
  },
        async created(){
            
      
            this.universalUsername = this.$route.query.universalUsername
            try {
            //Sunday is equal to 0
                if(this.currentDate.getDay() == 0){
                    this.sunday = this.currentDate
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                    console.log(this.sunday)
                    console.log(this.saturday)
                }else if(this.currentDate.getDay() == 1){
                    this.sunday.setDate(this.currentDate.getDate()-1)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }else if(this.currentDate.getDay() == 2){
                    this.sunday.setDate(this.currentDate.getDate()-2)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }else if(this.currentDate.getDay() == 3){
                    this.sunday.setDate(this.currentDate.getDate()-3)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }else if(this.currentDate.getDay() == 4){
                    this.sunday.setDate(this.currentDate.getDate()-4)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }else if(this.currentDate.getDay() == 5){
                    this.sunday.setDate(this.currentDate.getDate()-5)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }else if(this.currentDate.getDay() == 6){
                    this.sunday.setDate(this.currentDate.getDate()-6)
                    this.monday.setDate(this.sunday.getDate() + 1)
                    this.tuesday.setDate(this.sunday.getDate() + 2)
                    this.wednesday.setDate(this.sunday.getDate() + 3)
                    this.thursday.setDate(this.sunday.getDate() + 4)
                    this.friday.setDate(this.sunday.getDate() + 5)
                    this.saturday.setDate(this.sunday.getDate()+ 6)
                }

                this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
                this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
                this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
                this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
                this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
                this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
                this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)

                this.allEntries = this.sunEntries.concat(this.monEntries,this.tuesEntries,this.wedEntries,this.thursEntries,this.friEntries,this.satEntries)

                var temp = 0;
                for(temp; temp < this.allEntries.length; temp = temp + 1){
                    // console.log(this.allEntries[temp])
                    var holdEntry = await CalendarInfo.getEntryforCE(this.allEntries[temp].entryID)
                    
                    if(holdEntry[0].type == 'exercise'){
                        this.totalExercise += 1
                        this.totalEntries += 1
                        this.caloriesLost = this.caloriesLost + parseInt(holdEntry[0].calories)
                    }else{
                        this.totalMeal += 1
                        this.totalEntries += 1
                        this.caloriesGained = this.caloriesGained + parseInt(holdEntry[0].calories)
                    }
                }
                
                this.sunday.setDate(this.sunday.getDate()-7)
                this.monday.setDate(this.monday.getDate()-7)
                this.tuesday.setDate(this.tuesday.getDate()-7)
                this.wednesday.setDate(this.wednesday.getDate()-7)
                this.thursday.setDate(this.thursday.getDate()-7)
                this.friday.setDate(this.friday.getDate()-7)
                this.saturday.setDate(this.saturday.getDate()-7)

                this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
                this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
                this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
                this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
                this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
                this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
                this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)

                this.oneWeekEntries = this.sunEntries.concat(this.monEntries,this.tuesEntries,this.wedEntries,this.thursEntries,this.friEntries,this.satEntries)

                var temp1 = 0
                for(temp1; temp1 < this.oneWeekEntries.length; temp1 = temp1 + 1){
                    // console.log(this.allEntries[temp])
                    holdEntry = await CalendarInfo.getEntryforCE(this.oneWeekEntries[temp1].entryID)
                    
                    if(holdEntry[0].type == 'exercise'){
                        this.pastStats[0].numofexercise += 1
                        this.pastStats[0].totalentriescompleted += 1
                        this.pastStats[0].calorieslost = this.pastStats[0].calorieslost + parseInt(holdEntry[0].calories)
                    }else{
                        this.pastStats[0].numofmeals += 1
                        this.pastStats[0].totalentriescompleted += 1
                        this.pastStats[0].caloriesgained = this.pastStats[0].caloriesgained + parseInt(holdEntry[0].calories)
                    }
                }

                this.sunday.setDate(this.sunday.getDate()-7)
                this.monday.setDate(this.monday.getDate()-7)
                this.tuesday.setDate(this.tuesday.getDate()-7)
                this.wednesday.setDate(this.wednesday.getDate()-7)
                this.thursday.setDate(this.thursday.getDate()-7)
                this.friday.setDate(this.friday.getDate()-7)
                this.saturday.setDate(this.saturday.getDate()-7)

                this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
                this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
                this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
                this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
                this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
                this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
                this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)

                this.twoWeekEntries = this.sunEntries.concat(this.monEntries,this.tuesEntries,this.wedEntries,this.thursEntries,this.friEntries,this.satEntries)

                temp1 = 0
                for(temp1; temp1 < this.twoWeekEntries.length; temp1 = temp1 + 1){
                    // console.log(this.allEntries[temp])
                    holdEntry = await CalendarInfo.getEntryforCE(this.twoWeekEntries[temp1].entryID)
                    
                    if(holdEntry[0].type == 'exercise'){
                        this.pastStats[1].numofexercise += 1
                        this.pastStats[1].totalentriescompleted += 1
                        this.pastStats[1].calorieslost = this.pastStats[1].calorieslost + parseInt(holdEntry[0].calories)
                    }else{
                        this.pastStats[1].numofmeals += 1
                        this.pastStats[1].totalentriescompleted += 1
                        this.pastStats[1].caloriesgained = this.pastStats[1].caloriesgained + parseInt(holdEntry[0].calories)
                    }
                }

                this.sunday.setDate(this.sunday.getDate()-7)
                this.monday.setDate(this.monday.getDate()-7)
                this.tuesday.setDate(this.tuesday.getDate()-7)
                this.wednesday.setDate(this.wednesday.getDate()-7)
                this.thursday.setDate(this.thursday.getDate()-7)
                this.friday.setDate(this.friday.getDate()-7)
                this.saturday.setDate(this.saturday.getDate()-7)

                this.sunEntries = await CalendarInfo.getSundayEntries(this.sunday.toDateString(),this.universalUsername)
                this.monEntries = await CalendarInfo.getMondayEntries(this.monday.toDateString(),this.universalUsername)
                this.tuesEntries = await CalendarInfo.getTuesdayEntries(this.tuesday.toDateString(),this.universalUsername)
                this.wedEntries = await CalendarInfo.getWednesdayEntries(this.wednesday.toDateString(),this.universalUsername)
                this.thursEntries = await CalendarInfo.getThursdayEntries(this.thursday.toDateString(),this.universalUsername)
                this.friEntries = await CalendarInfo.getFridayEntries(this.friday.toDateString(),this.universalUsername)
                this.satEntries = await CalendarInfo.getSaturdayEntries(this.saturday.toDateString(),this.universalUsername)

                this.threeWeekEntries = this.sunEntries.concat(this.monEntries,this.tuesEntries,this.wedEntries,this.thursEntries,this.friEntries,this.satEntries)

                temp1 = 0
                for(temp1; temp1 < this.threeWeekEntries.length; temp1 = temp1 + 1){
                    // console.log(this.allEntries[temp])
                    holdEntry = await CalendarInfo.getEntryforCE(this.threeWeekEntries[temp1].entryID)
                    
                    if(holdEntry[0].type == 'exercise'){
                        this.pastStats[2].numofexercise += 1
                        this.pastStats[2].totalentriescompleted += 1
                        this.pastStats[2].calorieslost = this.pastStats[2].calorieslost + parseInt(holdEntry[0].calories)
                    }else{
                        this.pastStats[2].numofmeals += 1
                        this.pastStats[2].totalentriescompleted += 1
                        this.pastStats[2].caloriesgained = this.pastStats[2].caloriesgained + parseInt(holdEntry[0].calories)
                    }
                }

                console.log(this.pastStats)

                var exerciseEntries = {
                x: ['3 weeks ago', '2 weeks ago', '1 week ago', 'This week'],
                y: [this.pastStats[2].numofexercise, this.pastStats[1].numofexercise, this.pastStats[0].numofexercise, this.totalExercise],
                type: 'scatter',
                name:"Exercise Entries"
                };

                var mealEntries = {
                x: ['3 weeks ago', '2 weeks ago', '1 week ago', 'This week'],
                y: [this.pastStats[2].numofmeals, this.pastStats[1].numofmeals, this.pastStats[0].numofmeals, this.totalMeal],
                type: 'scatter',
                name:"Meal Entries"
                };

                var totalEntries = {
                x: ['3 weeks ago', '2 weeks ago', '1 week ago', 'This week'],
                y: [this.pastStats[2].totalentriescompleted, this.pastStats[1].totalentriescompleted, this.pastStats[0].totalentriescompleted, this.totalEntries],
                type: 'scatter',
                name:"Total Entries"
                };

                var layout = {
                    title: {
                        text:'Your Completed Entries',
                        font: {
                        family: 'Poppins',
                        size: 24
                        },
                        xref: 'paper',
                        x: 0.05,
                    },
                    xaxis: {
                        title: {
                        text: 'Past Weeks',
                        font: {
                            family: 'Poppins',
                            size: 18,
                            color: '#7f7f7f'
                        }
                        },
                    },
                    yaxis: {
                        title: {
                        text: 'Number of Entries',
                        font: {
                            family: 'Poppins',
                            size: 18,
                            color: '#7f7f7f'
                        }
                        }
                    }
                    };

                var data = [exerciseEntries, mealEntries, totalEntries];

                Plotly.newPlot('myPlot', data, layout);


                var caloriesGained = {
                x: ['3 weeks ago', '2 weeks ago', '1 week ago', 'This week'],
                y: [this.pastStats[2].caloriesgained, this.pastStats[1].caloriesgained, this.pastStats[0].caloriesgained, this.caloriesGained],
                type: 'scatter',
                name:"Gained"
                };

                var caloriesLost = {
                x: ['3 weeks ago', '2 weeks ago', '1 week ago', 'This week'],
                y: [this.pastStats[2].calorieslost, this.pastStats[1].calorieslost, this.pastStats[0].calorieslost, this.caloriesLost],
                type: 'scatter',
                name:"Lost"
                };

                var layout2 = {
                    title: {
                        text:'Your Calories Gained/Lost',
                        font: {
                        family: 'Poppins',
                        size: 24
                        },
                        xref: 'paper',
                        x: 0.05,
                    },
                    xaxis: {
                        title: {
                        text: 'Past Weeks',
                        font: {
                            family: 'Poppins',
                            size: 18,
                            color: '#7f7f7f'
                        }
                        },
                    },
                    yaxis: {
                        title: {
                        text: 'Total Calories',
                        font: {
                            family: 'Poppins',
                            size: 18,
                            color: '#7f7f7f'
                        }
                        }
                    }
                    };

                var data2 = [caloriesGained, caloriesLost];

                Plotly.newPlot('calories', data2, layout2);
        } catch (err) {
            this.error = err.message;
        }

        
      
  }
}
</script>

<style scoped>
