    img{
    width:20%;
    border-radius: 80px;
    }
    .basic-information{
    background-color:#1abc9c;
    border:1px solid steelblue;
	border-radius: 45px;
    margin: auto 5px;
    }
    ul{
    list-style-type: none;
    line-height:30px;
    }
    .this-week{
	border:1px solid rgb(103, 139, 255);
	border-radius: 45px;
	margin: 25px 25px;
