<template>
<div class = 'register'>
    <button class = 'button' v-on:click="Back">Back to Calendar</button>
    <h1 style>Create Entry</h1>
    <form>
        <label for="name">Enter the Name of the Entry:</label><br>
        <input type="text" id = "name" v-model="newEntry.name" placeholder="Name" required ="yes"><br>
        <label>Entry Type:<br>
        <select id ="type" v-model="newEntry.type" required = "yes">
            <option value="">Select a Type</option>
            <option value="exercise">Exercise</option>
            <option value="breakfast">Breakfast</option>
            <option value="lunch">Lunch</option>
            <option value="dinner">Dinner</option>
            <option value="snack">Snack</option>
        </select></label><br>
        <label for="description">Description:</label><br>
        <textarea id = "description" v-model="newEntry.description" required="yes"/><br>
        <label for="calories">Calories:<br>
        <input id = "calories" type=int v-model="newEntry.calories" placeholder="email" required="yes">
        </label><br><br>
        
        <button v-on:click="createEntry" class="button" type="button">Create Entry</button>
        
    </form>
</div>
</template>
<script>
import FormSubmission from '../FormSubmission';
// import CalendarInfo from '../CalendarInfo'

export default {
    name: 'CreateEntry',
    data(){
        return{
            newEntry: {
                name:"",
                type:"",
                description:"",
                calories:0,
                username:""
                },
            entryExists:[],
            universalUsername:''
            
            }
        },
        created(){
            this.universalUsername = this.$route.query.universalUsername
            this.newEntry.username = this.$route.query.universalUsername
        },
        methods: {
            async createEntry() {
                if(this.newEntry.name =='' || this.newEntry.type =='' || this.newEntry.description ==''){
                    alert("Please fill out all of the specified fields.")
                    
                }else{
                    this.entryExists = await FormSubmission.getEntry(this.newEntry.name,this.newEntry.type,this.newEntry.username)
                    console.log(this.entryExists[0])
                    if(this.entryExists[0] == undefined){
                        console.log(this.entryExists[0])
                        await FormSubmission.createEntry(this.newEntry)
                        alert("Your Entry has been successfully created.")
                        this.newEntry.name = ''
                        this.newEntry.type = ''
                        this.newEntry.description = ''
                        this.newEntry.calories = 0
                        this.$router.push({name:"Calendar",query:{universalUsername:this.universalUsername}})
                        
                    }else{
                        alert("An Entry has already been created with the specified name and type.")
                       
                    }
                }
            },
            Back(){
            this.$router.push({name:'Calendar',query:{universalUsername:this.universalUsername}})
        },
        }
        }
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
.register{
    padding:10px;
    text-align:center;
    font-family:"Poppins";
}
.button{
    color: white;
    border:1px solid black;
    background-color: #1abc9c;
    font-family:inherit;
}
label{
    font-weight:bold;
}
.submit{
    border:groove;
    border-radius: 40px;
    border-color:cornsilk;
    background-color:none;
    color:#1abc9c;
    font-size: 20px;
    font-family: inherit;
    font-weight: bold;
    padding:5px;
}
</style>