import axios from "axios";

const url = 'api/FormSubmission/';

 class FormSubmission {

    static getEntry(name,type,username) {
        return new Promise ((resolve,reject) => {
            axios.get(url,{params:{name:name,type:type,username:username}}).then((res) => {
                const data = res.data;
                resolve(
                    data.map(entry => ({
                        _id: entry._id,
                        type: entry.type,
                        name: entry.name,
                        description: entry.description,
                        calories: entry.calories,
                        username: entry.username,
                    }))
                );
                
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static checkName(object){
        return object.name == 'test'
    }
    
    //Create Entry
    static createEntry(newEntry){
        return axios.post(url,newEntry);
    }

    //Delete Entry
    static deleteEntry(name,username){
        return axios.delete(url,{
            data: {
                name:name,
                username:username
             }});
        
    }
    static updateEntry(currentName,currentType,username,newName,newType,newDescription,newCalories){
        return axios.put(url,{
            name: currentName,
            type: currentType,
            username:username,
            newName:newName,
            newType:newType,
            newDescription: newDescription,
            newCalories: newCalories,
            
        });
    }


    //User Functions
    static createUser(newUser){
        return axios.post(url + "user",newUser);
    }

    static checkUsername(username) {
        return new Promise ((resolve,reject) => {
            axios.get(url + "username",{params:{username:username}}).then((res) => {
                const data = res.data;
                resolve(
                    data.map(user => ({
                        username: user.username,
                        email: user.email,
                        
                        
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static checkEmail(email) {
        return new Promise ((resolve,reject) => {
            axios.get(url + "email",{params:{email:email}}).then((res) => {
                const data = res.data;
                resolve(
                    data.map(user => ({
                        username: user.username,
                        email: user.email,
                        
                        
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static loginEmail(email,password) {
        return new Promise ((resolve,reject) => {
            axios.get(url + "loginEmail",{params:{email:email,password:password}}).then((res) => {
                const data = res.data;
                resolve(
                    data.map(user => ({
                        username: user.username,
                        email: user.email,
                        
                        
                        
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }

    static loginUsername(username,password) {
        return new Promise ((resolve,reject) => {
            axios.get(url + "loginUsername",{params:{username:username,password:password}}).then((res) => {
                const data = res.data;
                resolve(
                    data.map(user => ({
                        username: user.username,
                        email: user.email,
                        
                        
                        
                    }))
                );
            })
            .catch((err)=> {
                reject(err);
            })
            
        });
    }
}


export default FormSubmission;