<template>
    <div class = 'homepage-entry'>
       <h3>{{homepageEntry.name}}
       <span><p>{{homepageEntry.description}}</p></span>
       <p>Calories: {{homepageEntry.calories}}</p>
       </h3>
    <button @click='onAdd()'>Add to Entries</button>
    </div>
</template>

<script>
    import CalendarInfo from '../CalendarInfo'
    import FormSubmission from '../FormSubmission'

    export default{
        name:'HomepageEntry',
    props:{
            homepageEntry:Object, 
            test: String  
        },
        data(){
            return{
                
                entryExists:[],
                
                
                }
            },
    methods:{
        async onAdd(){
            this.entryExists = await FormSubmission.getEntry(this.homepageEntry.name,this.homepageEntry.type,this.test)
            if(this.entryExists[0] == undefined){
            await CalendarInfo.createEntry({
                type:this.homepageEntry.type,
                name:this.homepageEntry.name,
                description:this.homepageEntry.description,
                calories:this.homepageEntry.calories,
                username:this.test
            })
            alert("Entry has been added to your collection. View it on the Calendar page.")
            }
            else{
                alert("You have already added the chosen entry.")
            }
        }
    },
    }
</script>
<style scoped>
.homepage-entry{
    border:2px groove;
    margin-bottom:30px;
    border-radius: 5px;
    background-color: #169c81;
    display:grid;
    grid-template-columns: 90% 10%;
}
.homepage-entry p{
    font-weight:normal;
}
.homepage-entry span{
    display:none;
}
.homepage-entry:hover span{
    display:block;
}
button{
    color:#1abc9c;
    font-size:20px;
    font-family: inherit;
}
button:hover{
    cursor:pointer;
}
</style>
