import {createRouter, createWebHistory} from 'vue-router'

//every page should be brought here with the path,
//name, and component values
import Calendar from '../components/Calendar.vue'
import Homepage from '../components/Homepage.vue'
import User from '../components/User.vue'
import CreateAccount from '../components/CreateAccount.vue'
import CreateEntry from '../components/CreateEntry.vue'
import UpdateEntry from '../components/UpdateEntry.vue'
import UpdateCalendarEntry from '../components/UpdateCalendarEntry.vue'
import Login from '../components/Login.vue'
import HelpPage from '../components/HelpPage.vue'

const routes = [
    {
        path:'/Calendar',
        name:'Calendar',
        component: Calendar,
    },
    {
        path:'/Homepage',
        name:'Homepage',
        component: Homepage,
    },
    {
        path:'/User',
        name:'User',
        component:User,
    },
    {
        path:'/CreateAccount',
        name:'CreateAccount',
        component:CreateAccount,
    },
    {
        path:'/CreateEntry',
        name:'CreateEntry',
        component:CreateEntry
    },
    {
        path:'/UpdateEntry',
        name:'UpdateEntry',
        component:UpdateEntry
    },
    {
        path:'/UpdateCalendarEntry',
        name:'UpdateCalendarEntry',
        component:UpdateCalendarEntry
    },
    {
        path:'/',
        name:'Login',
        component:Login
    },
    {
        path:'/HelpPage',
        name:'HelpPage',
        component:HelpPage
    }
    
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
})
export default router
