<template>
    <div :key="entry.id" v-for="entry in entries">
        <Entry @delete-entry="$emit('delete-entry', entry.name, entry.username)"
        :entry="entry"/>
    </div>
</template>

<script>
import Entry from './Entry'

export default {
    name:'Entries',
    props:{
        entries:Array,
        viewAllEntries:Boolean,
        viewExerciseEntries:Boolean,
        viewBreakfastEntries:Boolean,
        viewLunchEntries:Boolean,
        viewDinnerEntries:Boolean,
        viewSnackEntries:Boolean
    },
    components:{
        Entry,
    },
    emits:['delete-entry', 'toggle-view-entries','toggle-view-exercises', 'toggle-view-meals', 'toggle-view-breakfast', 'toggle-view-lunch', 'toggle-view-dinner', 'toggle-view-snack'],
}
</script>
