<template>
<div class = 'register'>
    
        <button v-on:click="Back" class="button">Back to Calendar</button>
    
    <h1>Update Calendar Entry</h1>
    <form>
        <label for="time">Change the time:</label><br>
         <select id='time' v-model="updatedCalendarEntry.hours">
		<option value="">Hours</option>
		<option value ="1">1</option>
		<option value ="2">2</option>
		<option value ="3">3</option>
		<option value ="4">4</option>
		<option value ="5">5</option>
		<option value ="6">6</option>
		<option value ="7">7</option>
		<option value ="8">8</option>
		<option value ="9">9</option>
		<option value ="10">10</option>
		<option value ="11">11</option>
		<option value ="12">12</option>
	</select>

    <select id='time' v-model="updatedCalendarEntry.minutes" type='text'>
		<option value="">Minutes</option>
		<option value ="00">00</option>
		
		<option value ="05">05</option>
		
		<option value ="10">10</option>
		
		<option value ="15">15</option>
		
		<option value ="20">20</option>
		
		<option value ="25">25</option>
		
		<option value ="30">30</option>

		<option value ="35">35</option>
		
		<option value ="40">40</option>

		<option value ="45">45</option>
		
		<option value ="50">50</option>

		<option value ="55">55</option>
	</select><br>
        <select id="AM/PM" v-model="updatedCalendarEntry.ampm">
            <option value="">Select AM or PM</option>
            <option value ="AM">AM</option>
            <option value ="PM">PM</option>
        </select>
        <br><br>
        
        <button v-on:click="updateEntry" class="button" type="button">Update Entry</button>
        
    </form>
</div>
</template>
<script>

import CalendarInfo from '../CalendarInfo'

//notes for tomorrow. Parse time from calendar page into hour/minutes/ampm store and load into fields using data objects.
//potentially rewrite code to use object id of entries to load them into calendar entries. 
//start login page.

export default {
    name: 'UpdateCalendarEntry',
    data(){
        return{
            updatedCalendarEntry: {
                time:'',
                day:'',
                date:'',
                hours:'',
                minutes:'',
                ampm:'',
                name:"",
                type:"",
                description:"",
                calories:0,
                username:""
                },
            currentCalendarEntry:{
                time:'',
                day:'',
                date:'',
                hours:'',
                minutes:'',
                ampm:'',
                name:"",
                type:"",
                description:"",
                calories:0,
                username:""
            },
            entryExists:[],
            universalUsername:''
            
            }
        },
        created(){
            
            this.currentCalendarEntry.username = this.$route.params.username
            this.updatedCalendarEntry.username = this.currentCalendarEntry.username
            this.currentCalendarEntry.date = this.$route.params.date
            this.updatedCalendarEntry.date = this.currentCalendarEntry.date
            this.currentCalendarEntry.time = this.$route.params.time
            this.updatedCalendarEntry.time = this.currentCalendarEntry.time
            this.currentCalendarEntry.day = this.$route.params.day
            this.updatedCalendarEntry.day = this.currentCalendarEntry.day
            var temp = this.currentCalendarEntry.time.split(":")
            var temp2 = temp[1].split(" ")
            this.currentCalendarEntry.hours = temp[0]
            this.currentCalendarEntry.minutes = temp2[0]
            this.currentCalendarEntry.ampm = temp2[1]
            this.updatedCalendarEntry.hours = temp[0]
            this.updatedCalendarEntry.minutes = temp2[0]
            this.updatedCalendarEntry.ampm = temp2[1]

            this.universalUsername=this.$route.query.universalUsername
            
            
        },
        methods: {
            async updateEntry() {
                if(this.updatedCalendarEntry.hours == '' || this.updatedCalendarEntry.minutes =='' || this.updatedCalendarEntry.ampm ==''){
                    alert("Please fill out all of the specified fields.")
                    
                }else{
                    this.entryExists = await CalendarInfo.getSpecificEntry(this.updatedCalendarEntry.date,this.updatedCalendarEntry.hours + ":" + this.updatedCalendarEntry.minutes + " " + this.updatedCalendarEntry.ampm)
                    
                    if(this.entryExists[0] == undefined){
                        this.updatedCalendarEntry.time = this.updatedCalendarEntry.hours + ":" + this.updatedCalendarEntry.minutes + " " + this.updatedCalendarEntry.ampm
                        
                        await CalendarInfo.updateCE(this.currentCalendarEntry.date,this.currentCalendarEntry.time, this.currentCalendarEntry.username, this.updatedCalendarEntry.time)
                        alert("Your Calendar Entry has been updated!")
                        this.$router.push({name:"Calendar",query:{universalUsername:this.universalUsername}})
                    }
                    else{
                        alert("An Entry has already been added at the specified date and time")
                    }
                  
                }
                
                
            },
            Back(){
            this.$router.push({name:'Calendar',query:{universalUsername:this.universalUsername}})
        },
            
        }
        }
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');
.register{
    padding:10px;
    text-align:center;
    font-family:"Poppins"
}
.button{
    color: white;
    border:1px solid black;
    background-color: #1abc9c;
    font-family:inherit;
}
label{
    font-weight:bold;
}
.submit{
    border:groove;
    border-radius: 40px;
    border-color:cornsilk;
    background-color:none;
    color:#1abc9c;
    font-size: 20px;
    font-family: inherit;
    font-weight: bold;
    padding:5px;
}
</style>