<template>
<div id = 'entry'
    @dragstart="dragStart"
    v-on:drag='dragging'
    draggable='true'
    class='draggable'
    @mouseover="hover">
    <h3 v-bind:id="entry.name + ':' + entry.type" >{{ entry.name }}
    <p style='font-weight:normal; text-align:left; margin-left:20px;'>Calories: {{entry.calories}}</p>
    <p style='font-weight:normal; text-align:left; margin-left:20px;'>Type: {{entry.type}}</p>
    <span>Description: {{entry.description}}</span>
    <i style ='float:right; margin: 0px 10px 0px 0;' @click ="onDelete(entry.name,entry.username)" class ="fas fa-times"></i>
    
    <button style='float:right;' @click='onEdit()'>Edit</button>
   
    </h3>
    <p style = "color:#1abc9c">.</p>
</div>
</template>
<script>
export default {
    name:'Entry',
    props:{
        entry: Object,
        entryName: String
    },
    data(){
        return{
            testEntry:{
                name:'',
                type:'',
                description:'',
                calories:0,
                username:''
            }
           
            
        }
    },
    methods:{
    onDelete(){
        this.$emit("delete-entry")
    },
    onEdit(){
    this.$router.push({name:'UpdateEntry', params:{
        name: this.testEntry.name,
        type: this.testEntry.type, 
        description: this.testEntry.description, 
        calories: this.testEntry.calories, 
        username: this.testEntry.username
        },
        query:{universalUsername:this.testEntry.username}})
    },
    hover(){
        this.testEntry.name = this.entry.name
        this.testEntry.type = this.entry.type
        this.testEntry.description = this.entry.description
        this.testEntry.calories = this.entry.calories
        this.testEntry.username = this.entry.username
        
    },
    dragStart:function(event){
    //grabs the HTML in text format (can be named whatever)
	event.dataTransfer.setData('text', event.target.innerHTML)
	},

	dragging:function(){
        console.log(" '"+ this.entry.name + "' is being dragged.")
	},
    }
}
</script>
<style scoped>
#entry{
    background: #1abc9c;
}
button{
    color:#1abc9c;
    border:1px groove;
}
button:hover{
    cursor:pointer;
}
.fas{
    color:red;
}
.fas:hover{
    cursor:pointer;
}
h3 span{
    font-weight:normal;
    text-align:left;
    display:none;
}
h3:hover p{
    display:none;
    height:10px;
    text-align:left;
}
h3:hover span{
    display:block;
    width:150px;
    height:100%;
    margin-left:20px;

}
</style>
