<template>
    <div class="sidebar" :style="{width: sidebarWidth}">
        <!--work here-->
        
			<button v-on:click="linkHomepage" v-if="collapsed"><img :src="require('@/assets/hut.png')" style = 'width:75%;'></button> 
		
            <button v-on:click="linkHomepage" v-else><img :src="require('@/assets/hut.png')" style = 'width:10%;'>FitHut</button>
        <br>
        
            <button v-on:click="linkCalendar" v-if="collapsed">
                <img :src="require('@/assets/cal-icon.png')" style = 'width:75%;'>
            </button>
            <button v-on:click="linkCalendar" v-else><img :src="require('@/assets/cal-icon.png')" style = 'width:10%;'>Calendar</button>  
		
        <br>
    <div class='user-button'>
        
        <button v-on:click="linkUser" v-if='collapsed' style='position:relative;top:390px;'>
            <img :src ="require('@/assets/profile-pic.jpg')" style = 'width:75%;'>
        </button>
    
		<button v-on:click="linkUser" v-else style='position:relative; top:390px;'>
            <img :src="require('@/assets/profile-pic.jpg')" style="width:10%;">
            Hello, User!
        </button>
		
    </div>
        <!--work here-->	

        <span 
        :class="{'rotate-180' : collapsed }"
        @click="toggleSidebar"
        style='position:relative;top:400px;'>
            <i>&#10094;&#10094;</i>
        </span>
    </div>
</template>

<script>
import {collapsed, toggleSidebar, sidebarWidth } from './SidebarState'
export default ({
    props:  {
        universalUsername: String
    },
    setup(){
        return{
            collapsed, 
            toggleSidebar,
            sidebarWidth
        }
    },
    methods:{
        linkHomepage(){
            this.$router.push({name:'Homepage',query:{universalUsername:this.universalUsername}})
        },
        linkCalendar(){
            this.$router.push({name:'Calendar',query:{universalUsername:this.universalUsername}})
        },
        linkUser(){
            this.$router.push({name:'User',query:{universalUsername:this.universalUsername}})
        }
    },

    created(){
        console.log(this.universalUsername)
    }
})
</script>

<style scoped>
.sidebar{
    color: white;
    background-color: var(--bg-color);

    left: 0;
    float: left;
    position: fixed;
    z-index: 1;
    top: 0;
    bottom: 0;
    padding: .5em;

    transition: .3s;

    display: flex;
    flex-direction: column;

}

.collapse-icon{
    position: absolute;
    bottom: 0;
    padding: 0.75em;

    color:white;

    transition: .1s linear;
}

.rotate-180{
    transform: rotate(180deg);

}
.sidebar button{
    border:none;
    font-family:Poppins;
    font-size:25px;
    font-weight:bold;
}
</style>
<style >
:root{
    --bg-color: #2f855a;
    --itemhover: #38a169;
    --item-active: #276749;
}
</style>